@import 'reset';
@import 'mixin';
@import 'grid';

body {
    font-size: 16px;
    font-family: "Roboto","Microsoft JhengHei","PingFangTC-Regular";
    .container {
        display: grid;
        grid-template: repeat(20, 1fr) / repeat(8, 1fr);
        height: 2000px;
        padding: 0;
        width: 960px !important;
        margin-top: 32px;
        margin-bottom: 32px;
        .title {
            grid-column: 3 / span 4;
            font-size: 20px;
            text-align: center;
            font-weight: bold;
            .title_logo {
                padding: 2px 8px;
                background-color: black;
                color: white;
                display: inline-block;
                margin-bottom: 7px; }
            .title_category {
                font-size: 72px; } }
        .product_item01 {
            display: grid;
            grid-area: 2 / 1 / span 6 / span 5;
            grid-template: repeat(6, 100px)/repeat(5, 120px);
            .item_text {
                grid-area: 2 / 1 / 3 / 5;
                position: relative;
                span {
                    position: absolute;
                    top: -14px;
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1); } }
            .item_img {
                grid-area: 2 / 1 / span 4 / span 4;
                width: 480px;
                height: 400px;
                @include bgSetting("https://images.unsplash.com/photo-1513827379601-9dba5099a3f9?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=1acdd78f34ee75aade9e313251bb1695&auto=format&fit=crop&w=634&q=80"); }
            .item_wrap {
                grid-area: 1 / 3 / span 3/ span 3;
                width: 240px;
                height: 200px;
                border: 20px solid #50E3C2;
                margin: 0 auto;
                z-index: -1;
                margin-top: 45px; }
            .item_number {
                grid-area: 6 / 1 / 7 / 2;
                font-size: 48px;
                font-weight: bold;
                color: #000; }
            .item_slash {
                grid-area: 5 / 1 / span 2 / span 3;
                width: 254px;
                height: 100px;
                margin: 50px 0 0 60px;
                background-size: 51px 51px;
                background-image: linear-gradient(135deg,#50E3C2 25%, transparent 25%, transparent 50%, #50E3C2 50%, #50E3C2 75%, transparent 75%, transparent); }
            .item_style {
                grid-area: 5 / 5 / span 2 / span 1;
                span {
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic;
                    writing-mode: vertical-lr; } } }
        .product_item02 {
            display: grid;
            grid-area: 3 / -1 / 7 / -5;
            grid-template: repeat(5, 100px)/repeat(3, 120px);
            .item_text {
                grid-area: 4 / 3 / 5 / 5;
                position: relative;
                span {
                    position: absolute;
                    top: 25px;
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1); } }
            .item_img {
                grid-area: 2 / -3 / span 2 / span 2;
                width: 240px;
                height: 200px;
                @include bgSetting("https://images.unsplash.com/photo-1514632542677-48fae74a01b2?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=cf412aceb1d689765e387d30fca7210a&auto=format&fit=crop&w=634&q=80"); }
            .item_wrap {
                grid-area: 1 / 3 / span 2 / span 2;
                width: 240px;
                height: 200px;
                border: 20px solid #50E3C2;
                margin: 0 auto;
                z-index: -1; }
            .item_number {
                grid-area: 1 / 4 / 2 / 5;
                font-size: 48px;
                font-weight: bold;
                color: #000;
                text-align: right; }
            .item_square {
                grid-area: 1 / 2 / 4 / 5;
                width: 240px;
                height: 200px;
                background-color: #50E3C2;
                margin: 0 auto;
                margin-top: 50px;
                z-index: -1; }
            .item_style {
                grid-area: 1 / 1 / span 1 / span 2;
                position: relative;
                span {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic; } } }
        .product_item03 {
            display: grid;
            grid-area: 8 / 1 / span 4 / 6;
            grid-template: repeat(4, 100px)/repeat(5, 120px);
            .item_text {
                grid-area: 1 / 2 / 2 / 5;
                position: relative;
                span {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1); } }
            .item_img {
                grid-area: 2 / 1 / span 2 / span 4;
                width: 480px;
                height: 200px;
                @include bgSetting("https://images.unsplash.com/photo-1496345875659-11f7dd282d1d?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=a0f994eef47e5fb1a67849703cc961b3&auto=format&fit=crop&w=1350&q=80"); }
            .item_number {
                grid-area: 4 / 5 / 5 / 6;
                font-size: 48px;
                font-weight: bold;
                color: #000;
                text-align: left; }
            .item_wrap {
                grid-area: 2 / 3 / span 3 / span 3;
                width: 240px;
                height: 200px;
                border: 20px solid #50E3C2;
                margin: 0 auto;
                margin-top: 50px;
                z-index: -1; }
            .item_square {
                grid-area: 1 / 1 / 3 / 3;
                width: 240px;
                height: 100px;
                background-color: #50E3C2;
                margin-top: 50px;
                z-index: -1; }
            .item_style {
                grid-area: 4 / 1 / span 1 / span 2;
                position: relative;
                span {
                    position: absolute;
                    right: 0;
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic; } } }
        .product_item04 {
            display: grid;
            grid-area: 7 / 6 / span 7 / span 3;
            grid-template: repeat(7, 100px)/repeat(3, 120px);
            .item_text {
                grid-area: 3 / 1 / span 2 / span 1;
                position: relative;
                span {
                    position: absolute;
                    top: 50px;
                    left: 25px;
                    transform: rotate(-90deg);
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1); } }
            .item_img {
                grid-area: 3 / 2 / span 4 / span 1;
                width: 240px;
                height: 400px;
                @include bgSetting("https://images.unsplash.com/photo-1484516758160-69878111a911?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=39466d66952b133d5a5cff642b970d05&auto=format&fit=crop&w=687&q=80"); }
            .item_wrap {
                grid-area: 2 / 1 / span 2/ span 3;
                width: 240px;
                height: 200px;
                border: 20px solid #50E3C2;
                margin: 0 auto;
                z-index: -1; }
            .item_number {
                grid-area: 6 / 1 / 7 / 2;
                font-size: 48px;
                font-weight: bold;
                color: #000;
                position: relative;
                span {
                    position: absolute;
                    bottom: 0;
                    right: 0; } }
            .item_slash {
                grid-area: 6 / 2 / 7 / span 2;
                height: 100px;
                margin-top: 50px;
                background-size: 51px 51px;
                background-image: linear-gradient(135deg,#50E3C2 25%, transparent 25%, transparent 50%, #50E3C2 50%, #50E3C2 75%, transparent 75%, transparent); }
            .item_style {
                grid-area: 1 / 3 / span 2 / span 1;
                text-align: center;
                span {
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic;
                    writing-mode: vertical-lr;
                    margin-top: 50px; } } }
        .product_item05 {
            display: grid;
            grid-area: 12 / 1 / span 4 / span 6;
            grid-template: repeat(4, 100px)/repeat(6, 120px);
            .item_text {
                grid-area: 2 / 1 / span 1 / span 2;
                position: relative;
                span {
                    position: absolute;
                    bottom: 0;
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1); } }
            .item_img {
                grid-area: 2 / 3 / span 2 / span 2;
                width: 240px;
                height: 200px;
                @include bgSetting("https://images.unsplash.com/photo-1525448198276-222de3961638?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=43fb25556cdbd191e520c7f28739e40d&auto=format&fit=crop&w=1350&q=80"); }
            .item_wrap {
                grid-area: 3 / 2 / span 2/ span 2;
                width: 240px;
                height: 100px;
                border: 20px solid #50E3C2;
                margin-top: 50px;
