            .item_number {
                grid-area: 3 / 2 / 4 / 3;
                font-size: 48px;
                font-weight: bold;
                color: #000;
                position: relative;
                span {
                    position: absolute;
                    bottom: 0; } }
            .item_slash {
                grid-area: 1 / 1 / span 2 / span 3;
                width: 240px;
                height: 100px;
                margin: 0 auto;
                margin-top: 50px;
                background-size: 51px 51px;
                background-image: linear-gradient(135deg,#50E3C2 25%, transparent 25%, transparent 50%, #50E3C2 50%, #50E3C2 75%, transparent 75%, transparent); }
            .item_style {
                grid-area: 2 / 5 / span 1 / span 2;
                span {
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic; } } }
        .product_item06 {
            display: grid;
            grid-area: 14 / 4 / span 6 / span 5;
            grid-template: repeat(6, 100px)/repeat(5, 120px);
            .item_text {
                grid-area: 1 / 3 / span 1 / span 3;
                position: relative;
                span {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1); } }
            .item_img {
                grid-area: 2 / 2 / span 2 / span 2;
                width: 480px;
                height: 400px;
                @include bgSetting("https://images.unsplash.com/photo-1519406709381-c1f293304b28?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=98c497a29f229085d9574ee8b6eeef8c&auto=format&fit=crop&w=634&q=80"); }
            .item_number {
                grid-area: 3 / 1 / 4 / 2;
                font-size: 48px;
                font-weight: bold;
                color: #000;
                position: relative;
                span {
                    position: absolute;
                    right: 0;
                    bottom: 0; } }
            .item_wrap {
                grid-area: 5 / 2 / span 2 / span 4;
                height: 100px;
                border: 20px solid #50E3C2;
                margin-top: 50px;
                z-index: -1; }
            .item_square {
                grid-area: 4 / 1 / span 2 / span 2;
                width: 120px;
                background-color: #50E3C2;
                margin: 0 auto;
                z-index: -1; }
            .item_style {
                grid-area: 6 / 3 / span 1 / span 2;
                text-align: center;
                span {
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic; } } }
        .product_item07 {
            display: grid;
            grid-area: 16 / 1 / span 5 / span 2;
            grid-template: repeat(5, 100px)/repeat(3, 120px);
            .item_text {
                grid-area: 2 / 3 / span 2 / span 1;
                position: relative;
                span {
                    position: absolute;
                    bottom: 85px;
                    right: 5px;
                    font-size: 48px;
                    color: #000000;
                    font-weight: bold;
                    z-index: 2;
                    text-shadow: 0px -15px 0px rgba(0,0,0,0.3), 0px -30px 0px rgba(0,0,0,0.1);
                    transform: rotate(-270deg); } }
            .item_img {
                grid-area: 2 / 1 / span 4 / span 2;
                width: 240px;
                height: 400px;
                @include bgSetting("https://images.unsplash.com/photo-1516914943479-89db7d9ae7f2?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=63ff45d79414b3166b08b800a32018be&auto=format&fit=crop&w=634&q=80"); }
            .item_number {
                grid-area: 5 / 3 / 6 / 4;
                font-size: 48px;
                font-weight: bold;
                color: #000;
                position: relative;
                span {
                    position: absolute;
                    right: 0;
                    bottom: 0; } }
            .item_wrap {
                grid-area: 1 / 1 / 4 / span 3;
                width: 240px;
                height: 400px;
                border: 20px solid #50E3C2;
                margin: 0 auto;
                margin-top: 50px;
                z-index: -1; }
            .item_square {
                grid-area: 4 / 1 / span 2 / span 2;
                width: 120px;
                background-color: #50E3C2;
                margin: 0 auto;
                z-index: -1; }
            .item_style {
                grid-area: 1 / 1 / span 1 / span 2;
                position: relative;
                span {
                    font-size: 24px;
                    font-weight: bold;
                    font-style: italic;
                    position: absolute;
                    bottom: 0;
                    left: 0; } } } } }
